












import { Component, Prop, Emit, PropSync, Vue } from 'vue-property-decorator';

@Component
export default class KbnButton extends Vue {
@Prop({default: 'button'})
type!: 'text' | 'button';

@Prop({ default: false })
disabled!: boolean;

@Emit('click')
handleClick(event: any) {
  return event;
}

get classes(): string {
  const cls = this.type === 'text' ? ('-' + this.type) : '';
  return `kbn-button${cls}`;
}

}

